import React from "react";
import { Link } from "react-router-dom";
import {
  Navbar,
  Collapse,
  Nav,
  NavItem,
  NavbarBrand,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Dropdown,
  Button,
} from "reactstrap";
import { ReactComponent as LogoWhite } from "../assets/images/logos/xtremelogowhite.svg";
import user1 from "../assets/images/users/user1.jpg";
import '../views/Style.css';
import { useDispatch, useSelector } from "react-redux";
import logo from '../assets/images/logos/image1233333.png';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FaPlusCircle } from 'react-icons/fa';
import 'bootstrap-icons/font/bootstrap-icons.css';
import { FaSearch } from 'react-icons/fa';
import { IconContext } from 'react-icons';
import { useState , useEffect} from "react";
import { fetchHeaderProfile ,fetchadminHeaderProfile } from "../action/action";
const Header = () => {
  const dispatch = useDispatch()
  const [isOpen, setIsOpen] = React.useState(false);
  // const [role, setRole] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const [dropdownOpen, setDropdownOpen] = React.useState(false);
  const headerprofile = useSelector((state) => state.login.headerprofile);
  const adminheaderprofile = useSelector((state) => state.login.adminheaderprofie);
  console.log(adminheaderprofile)
  console.log(headerprofile)

  useEffect(() => {
   dispatch(fetchHeaderProfile())
   dispatch(fetchadminHeaderProfile())
  }, []);
  const toggle = () => setDropdownOpen((prevState) => !prevState);
  
  const Handletoggle = () => {
    setIsOpen(!isOpen);
  };
  const showMobilemenu = () => {
    document.getElementById("sidebarArea").classList.toggle("showSidebar");
  };

function handlelogout(){
  localStorage.clear();
}
useEffect(() => {
  // Get the role from local storage and set it in the state

  // const roleFromLocalStorage = localStorage.getItem("role");
  // setRole(roleFromLocalStorage);
  // console.log(roleFromLocalStorage)
  setIsLoading(false);
}, []);
const role = JSON.parse(localStorage.getItem("role"));
let id = localStorage.getItem("id");
console.log(id)
  console.log(role)
  if (!role) {
    return <div>Loading...</div>;
  }
  if (isLoading) {
    // Show loading message until the role is available
    return <div>Loading...</div>;
  }
  console.log(role)
  // if(!headerprofile){
  //   return <div>loading</div>
  // }
  return (
    <Navbar color="primary" dark expand="md" className="fixed-navbar">
      <div className="d-flex align-items-center">
      <img  src={logo} alt="Logo" className="logo" />
      <Link to="/starter" className="nav-linkab" >
              HRMS
            </Link>
        <NavbarBrand href="/" className="d-lg-none">
          {/* <LogoWhite /> */}
        </NavbarBrand>

      </div>
     
      <div className="hstack gap-2" >
        <Button
          color="primary"
          size="sm"
          className="d-sm-block d-md-none"
          onClick={Handletoggle}
        >
          {isOpen ? (
            <i className="bi bi-x"></i>
          ) : (
            <i className="bi bi-three-dots-vertical"></i>
          )}
        </Button>
      </div>
      
      <Collapse navbar isOpen={isOpen}>
        <Nav className="me-auto" navbar>
      
        {role == 'Employee' ? (
            <>
              <NavItem>
                <Link to="/employees" className="nav-link">
                  Employees
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/timeoff" className="nav-link">
                  Time off
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/attendance" className="nav-link">
                  Attendance
                </Link>
              </NavItem>
            </>
          ) : ( 
            <> 
              <NavItem>
                <Link to="/manageemployee" className="nav-link">
                Employees
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/admintimeoff" className="nav-link">
                Time off
                </Link>
              </NavItem>
              <NavItem>
                <Link to="/adminattendance" className="nav-link">
                Attendance
                </Link>
              </NavItem>
            </>
          )}
        </Nav>
     
      </Collapse>
      <div className="hstack gap-2">
    <FaPlusCircle className="fa-beat" />
    {/* <i className="bi bi-search" ></i> */}
    {/* <IconContext.Provider className="Searchicon" value={{ color: '#FFFFFF' }}> */}
    <FaSearch className="Searchicon" />
    {/* </IconContext.Provider> */}
  </div>
      <Dropdown isOpen={dropdownOpen} toggle={toggle}>
          <DropdownToggle color="primary">
            <img
             src={headerprofile?.featuredImage || adminheaderprofile?.featuredImage}
              alt="profile"
              className="rounded-circle"
              width="35"
            ></img>
          </DropdownToggle>
          <DropdownMenu>
          <div className="dropdown-header1">
          <span className="header-image">
            <img src={headerprofile?.featuredImage || adminheaderprofile?.featuredImage}  width="80"></img>
          </span>
            <DropdownItem header>{headerprofile?.fullName || adminheaderprofile?.fullName}</DropdownItem>
            {headerprofile ? (
            <Link to="/profile" style={{ textDecoration: 'none' }}>
              <DropdownItem className="dropitems">{headerprofile.email}</DropdownItem>
            </Link>
          ) : null}
          
          {adminheaderprofile ? (
            <Link to="/employeeprofile" style={{ textDecoration: 'none' }}>
              <DropdownItem className="dropitems">{adminheaderprofile.email}</DropdownItem>
            </Link>
          ) : null}
   
       
            <DropdownItem divider />
         <Link to="/"  style={{textDecoration: 'none'}} ><DropdownItem onClick={handlelogout}>Logout</DropdownItem></Link>   
         </div>
          </DropdownMenu>
        </Dropdown>
    </Navbar >
  );
};
export default Header;